.wrapper {
  display: flex;
  flex-direction: column;
  width: fit-content;
}

.inputWrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.globalInput {
  border: 1px solid;
  padding-left: 15px;
  width: 100%;
  height: 50px;
  font-size: 25px;
  background-color: rgb(209, 211, 226);
  border-radius: 10px;
}

.add {
  padding: 14px 30px;
  border-radius: 10px;
  background-image: linear-gradient(90deg, #a618d1, #b2bd15);
  font: bold italic 1.2rem "Fira Sans", serif;
  margin: 12px 0px 15px 20px;
  cursor: pointer;
  user-select: none;
}

.gap {
    width: 20px;
  }
